import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import GoldPageChart from './GoldPageChart';
import GoldCalculator from './GoldCalculator';
import './GoldPage.scss';

@inject('gold')
@observer
class GoldPage extends Component {
  delayPageMove = (pageName, e) => {
    e.preventDefault();
    const {
      history: { push }
    } = this.props;
    const { common } = this.props.gold.root;
    common.loadingDelay(push, pageName);
  };

  render() {
    const { delayPageMove } = this;
    const { gold } = this.props;

    const buyChartList = gold.buySise.map((info, index) => {
      return parseInt(info.buyGold.replace(/,/g, ''));
    });
    const sellChartList = gold.sellSise.map((info, index) => {
      return parseInt(info.gold24.replace(/,/g, ''));
    });

    const dateList = gold.week.map((info, index) => {
      return (
        <td key={index} className="tit">
          {info}
        </td>
      );
    });
    const buyGoldList = gold.buySise.map((info, index) => {
      return <td key={index}>{info.buyGold}</td>;
    });
    const buyPrepList = gold.buySise.map((info, index) => {
      return (
        <td key={index}>
          {info.fluctuation === 'up' ? (
            <span className="up"> {info.buyPrep}</span>
          ) : info.fluctuation === 'down' ? (
            <span className="down"> {info.buyPrep}</span>
          ) : (
            <span>- {info.buyPrep}</span>
          )}{' '}
        </td>
      );
    });
    const gold24 = gold.sellSise.map((info, index) => {
      return <td key={index}>{info.gold24}</td>;
    });
    const gold18 = gold.sellSise.map((info, index) => {
      return <td key={index}>{info.gold18}</td>;
    });
    const gold14 = gold.sellSise.map((info, index) => {
      return <td key={index}>{info.gold14}</td>;
    });

    return (
      <div className="GoldPage">
        <h2> </h2>

        <GoldCalculator />

        <div className="select">
          <select>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
            <option value="7">7</option>
            <option value="8">8</option>
            <option value="9">9</option>
            <option value="10">10</option>
          </select>
           
        </div>

        <GoldPageChart
          weekNotYearData={gold.weekNotYear}
          buyData={buyChartList}
          sellData={sellChartList}
        />

        <p className="right">: , 3.75g(1)</p>
        <table className="goldTable">
          <colgroup>
            <col />
            <col />
            <col />
            <col />
            <col />
            <col />
            <col />
            <col />
            <col />
          </colgroup>
          <tbody>
            <tr>
              <td rowSpan="3" className="tbg">
                
              </td>
              <td className="tit" />
              {dateList}
            </tr>
            <tr>
              <td className="sit"></td>
              {buyGoldList}
            </tr>
            <tr>
              <td className="sit"></td>
              {buyPrepList}
            </tr>
            <tr>
              <td rowSpan="3" className="bbg">
                
              </td>
              <td className="sit"></td>
              {gold24}
            </tr>
            <tr>
              <td className="sit">18K</td>
              {gold18}
            </tr>
            <tr>
              <td className="sit">14K</td>
              {gold14}
            </tr>
          </tbody>
        </table>

        <div className="home_btn">
          <p>
            :{' '}
            <a
              href="http://www.koreagoldx.co.kr/"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </a>
          </p>
          <Link
            to={{
              pathname: ''
            }}
            onClick={e => delayPageMove('', e)}
          >
            
          </Link>
        </div>
      </div>
    );
  }
}

export default GoldPage;
