import { observable, action, computed } from 'mobx';
import * as api from '../lib/api';
import cheerio from 'cheerio';

export default class goldStore {
  @observable date = new Date();
  @observable week = []; //--
  @observable weekNotYear = []; //-
  @observable.ref buySise = []; //  -   
  @observable sellSise = []; //  -   
  @observable calculatorToggle = false; //  
  @observable buyGoldValue = null; //   
  @observable todayBuySise = null; //    -     
  @observable todaySellSise = null; //    -     

  constructor(root) {
    this.root = root;

    this.actGoldList();
  }

  @action
  actGoldList = async weekValue => {
    try {
      this.root.common.loading = true;

      const response = await api.goldSise();
      console.log(response.data);

      this.root.common.loading = false;
    } catch (e) {
      console.log(e);
    }
  };

  @action
  actGoldToggleTrue = () => {
    this.calculatorToggle = true;
  };
  @action
  actGoldToggleFalse = () => {
    this.calculatorToggle = false;
  };
  @action
  actGoldValue = value => {
    value ? (this.buyGoldValue = parseInt(value)) : (this.buyGoldValue = 0);
  };

  @computed
  get buyTotal() {
    return (this.buyGoldValue * this.todayBuySise) / 3.75;
  }
  @computed
  get sellTotal() {
    return (this.buyGoldValue * this.todaySellSise) / 3.75;
  }
}

